
.etic-btn {
    border:0px;
    min-width: 130px;
    height: 30px;
    display:inline-block;
    font-family: 'Arial Bold', 'Arial';
    font-size:1em;
    font-weight:bold;
    color:#6F6F6F;
    text-decoration:none;
    text-transform:uppercase;
    text-align:center;
    line-height: 27px;
    position:relative;
    cursor:pointer;
    background: #F8F8F8;
    -webkit-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.25);
    -moz-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.25);
    box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.25);
    text-align: left;
  }
  .etic-btn-span {
    margin-left: 5px;
    font-size: 0.75em;
    line-height: 30px;
    vertical-align: middle;
  }
  .etic-btn-check {
     color:#FFFFFF;
     background: #258900;
     -webkit-box-shadow: inset 0px 0px 4px 1px rgba(22,70,2,1);
     -moz-box-shadow: inset 0px 0px 4px 1px rgba(22,70,2,1);
      box-shadow: inset 0px 0px 4px 1px rgba(22,70,2,1);
  }

  .etic-icon-rem{
    font-size: 20px;
    vertical-align: middle;
    font-weight: bold;
    line-height: 30px;
    /* padding-left: 5px; */
    /* padding-top: 1px; */
    background-color: transparent;
    border: none;
  }
  .etic-icon-check{
    font-size: 12px;
    vertical-align: middle;
    font-weight: bold;
    line-height: 30px;
    padding-left: 5px;
    padding-top: 1px;
  }
  button:active span, button:focus span ,button:active i, button:focus i ,button:active div, button:focus div{
    position:relative;
    top: 0;
    left: 0;
  }